.wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 60px;
  width: 100%;
  overflow-y: auto;
  overflow-x: hidden;
  border-top: 1px solid #dce1e6;
  background-color: #fafbfc;
}
.newMessage {
  padding: 8px 10px;
  vertical-align: middle;
  resize: none;
  height: 36px;
  border: 1px solid #d3d9de;
  border-radius: 6px;
  width: 480px;
}
.addMessage {
  cursor: pointer;
  color: #99a2ad;
  display: block;
  padding-left: 30px;
  background-color: inherit;
  &:hover {
    color: #818c99;
  }
}
